.grid-container {
  display: flex;
  flex-direction: column;
  padding: 60px 150px;
  align-items: center;
  justify-content: center;
  margin: 30px;
  border: 1px solid;
}

.grid-row {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding: 26px;
  align-items: center;
}

.grid-item {
  padding: 10px;
  border: 1px solid;
  text-align: center;
}

.text-editor {
  display: flex;
  align-items: center;
  margin: 5px 15px;
  border: 1px solid;
  padding: 1%;
}

.text-editor button {
  margin-right: 5px;
  padding: 5px 10px;
  border: 1px solid #ddd;
  border-radius: 3px;
  cursor: pointer;
}

.text-box {
  font-size: 16px;
  border: 1px solid;
  padding: 1.5%;
  margin: 10px 70px;
}

.text-box input {
  border: none;
}
